module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport { spacing, dimensions, position, layout } from 'ui-box'\nimport { withTheme } from '../../theme'\nimport { Pane } from '../../layers'\nimport { Heading, Paragraph } from '../../typography'\nimport { IconButton } from '../../buttons'\nimport { Icon } from '../../icon'\n\nclass Alert extends PureComponent {\n  static propTypes = {\n    /**\n     * Composes some Box APIs.\n     */\n    ...spacing.propTypes,\n    ...position.propTypes,\n    ...layout.propTypes,\n    ...dimensions.propTypes,\n\n    /**\n     * The content of the alert. When a string is passed it is wrapped in a `<Text size={400} />` component.\n     */\n    children: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),\n\n    /**\n     * The intent of the alert.\n     */\n    intent: PropTypes.oneOf(['none', 'success', 'warning', 'danger'])\n      .isRequired,\n\n    /**\n     * The title of the alert.\n     */\n    title: PropTypes.node,\n\n    /**\n     * When true, show a border on the left matching the type.\n     */\n    hasTrim: PropTypes.bool,\n\n    /**\n     * When true, show a icon on the left matching the type,\n     */\n    hasIcon: PropTypes.bool,\n\n    /**\n     * When true, show a remove icon button.\n     */\n    isRemoveable: PropTypes.bool,\n\n    /**\n     * Function called when the remove button is clicked.\n     */\n    onRemove: PropTypes.func,\n\n    /**\n     * The appearance of the alert.\n     */\n    appearance: PropTypes.oneOf(['default', 'card']),\n\n    /**\n     * Theme provided by ThemeProvider.\n     */\n    theme: PropTypes.object.isRequired\n  }\n\n  static defaultProps = {\n    intent: 'none',\n    hasTrim: true,\n    hasIcon: true,\n    isRemoveable: false,\n    appearance: 'default'\n  }\n\n  getIconForIntent = intent => {\n    const { theme } = this.props\n\n    return <Icon size={14} {...theme.getIconForIntent(intent)} />\n  }\n\n  render() {\n    const {\n      theme,\n\n      title,\n      intent,\n      hasTrim,\n      hasIcon,\n      children,\n      appearance,\n      isRemoveable,\n      onRemove,\n      ...props\n    } = this.props\n\n    /**\n     * Note that Alert return a className and additional properties.\n     */\n    const { className, ...themeProps } = theme.getAlertProps({\n      appearance,\n      intent,\n      hasTrim\n    })\n\n    return (\n      <Pane\n        className={className}\n        role=\"alert\"\n        backgroundColor=\"white\"\n        overflow=\"hidden\"\n        position=\"relative\"\n        display=\"flex\"\n        paddingY={12}\n        paddingX={16}\n        {...themeProps}\n        {...props}\n      >\n        {hasIcon && (\n          <Pane\n            marginRight={10}\n            marginLeft={2}\n            height={14}\n            display=\"block\"\n            marginTop={2}\n          >\n            {this.getIconForIntent(intent)}\n          </Pane>\n        )}\n        <Pane display=\"flex\" width=\"100%\">\n          <Pane flex={1}>\n            <Heading\n              is=\"h4\"\n              fontWeight={600}\n              size={400}\n              marginTop={0}\n              marginBottom={0}\n            >\n              {title}\n            </Heading>\n            {typeof children === 'string' ? (\n              <Paragraph size={400} color=\"muted\">\n                {children}\n              </Paragraph>\n            ) : (\n              children\n            )}\n          </Pane>\n          {isRemoveable && (\n            <Pane\n              marginLeft={24}\n              flexShrink={0}\n              marginBottom={-2}\n              marginTop={-2}\n              marginRight={-2}\n            >\n              <IconButton\n                icon=\"cross\"\n                appearance=\"minimal\"\n                height={24}\n                onClick={onRemove}\n              />\n            </Pane>\n          )}\n        </Pane>\n      </Pane>\n    )\n  }\n}\n\nexport default withTheme(Alert)\n"