module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport { Text } from '../../typography'\nimport { Icon } from '../../icon'\nimport TableHeaderCell from './TableHeaderCell'\n\nconst invisibleInput = {\n  border: 'none',\n  backgroundColor: 'transparent',\n  WebkitAppearance: 'none',\n  WebkitFontSmoothing: 'antialiased',\n\n  '&:focus': {\n    outline: 'none'\n  },\n\n  '&::placeholder': {\n    color: `rgba(67, 90, 111, 0.7)`\n  }\n}\n\nexport default class SearchTableHeaderCell extends PureComponent {\n  static propTypes = {\n    /**\n     * Composes the TableHeaderCell component as the base.\n     */\n    ...TableHeaderCell.propTypes,\n\n    /**\n     * The value of the input.\n     */\n    value: PropTypes.string,\n\n    /**\n     * Handler to be called when the input changes.\n     */\n    onChange: PropTypes.func,\n\n    /**\n     * Sets whether the component should be automatically focused on component render.\n     */\n    autoFocus: PropTypes.bool,\n\n    /**\n     * Sets whether to apply spell checking to the content.\n     */\n    spellCheck: PropTypes.bool,\n\n    /**\n     * Text to display in the input if the input is empty.\n     */\n    placeholder: PropTypes.string\n  }\n\n  static defaultProps = {\n    onChange: () => {},\n    spellCheck: true,\n    placeholder: 'Filter...'\n  }\n\n  render() {\n    const {\n      value,\n      children,\n      onChange,\n      autoFocus,\n      spellCheck,\n      placeholder,\n      ...props\n    } = this.props\n\n    return (\n      <TableHeaderCell {...props}>\n        <Icon\n          icon=\"search\"\n          color=\"muted\"\n          marginLeft={2}\n          marginRight={10}\n          size={12}\n        />\n        <Text\n          is=\"input\"\n          size={300}\n          flex=\"1\"\n          css={invisibleInput}\n          value={value}\n          onChange={e => onChange(e.target.value)}\n          autoFocus={autoFocus}\n          spellCheck={spellCheck}\n          fontWeight={500}\n          marginLeft={-2}\n          paddingLeft={0}\n          placeholder={placeholder}\n        />\n      </TableHeaderCell>\n    )\n  }\n}\n"