module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport { splitBoxProps } from 'ui-box'\nimport { FormField } from '../../form-field'\nimport TextInput from './TextInput'\n\nlet idCounter = 0\n\nexport default class TextInputField extends PureComponent {\n  static propTypes = {\n    /**\n     * Composes the TextInput component as the base.\n     */\n    ...TextInput.propTypes,\n    ...FormField.propTypes,\n\n    /**\n     * The label used above the input element.\n     */\n    label: PropTypes.node.isRequired,\n\n    /**\n     * Passed on the label as a htmlFor prop.\n     */\n    labelFor: PropTypes.string,\n\n    /**\n     * Wether or not show a asterix after the label.\n     */\n    isRequired: PropTypes.bool,\n\n    /**\n     * A optional description of the field under the label, above the input element.\n     */\n    description: PropTypes.node,\n\n    /**\n     * A optional hint under the input element.\n     */\n    hint: PropTypes.node,\n\n    /**\n     * If a validation message is passed it is shown under the input element\n     * and above the hint.\n     */\n    validationMessage: PropTypes.node,\n\n    /**\n     * The height of the input element.\n     */\n    inputHeight: PropTypes.number,\n\n    /**\n     * The width of the input width.\n     */\n    inputWidth: PropTypes.oneOfType([PropTypes.number, PropTypes.string])\n  }\n\n  static defaultProps = {\n    /**\n     * The input width should be as wide as the form field.\n     */\n    inputWidth: '100%',\n    inputHeight: 32\n  }\n\n  state = {\n    id: (this.props.id || idCounter++).toString()\n  }\n\n  render() {\n    const {\n      // We are using the id from the state\n      id: unusedId,\n\n      // FormField props\n      hint,\n      label,\n      description,\n      validationMessage,\n\n      // TextInput props\n      inputHeight,\n      inputWidth,\n      disabled,\n      required,\n      isInvalid,\n      appearance,\n      placeholder,\n      spellCheck,\n\n      // Rest props are spread on the FormField\n      ...props\n    } = this.props\n\n    const id = `TextInputField-${this.state.id}`\n\n    /**\n     * Split the wrapper props from the input props.\n     */\n    const { matchedProps, remainingProps } = splitBoxProps(props)\n\n    return (\n      <FormField\n        marginBottom={24}\n        label={label}\n        isRequired={required}\n        hint={hint}\n        description={description}\n        validationMessage={validationMessage}\n        labelFor={id}\n        {...matchedProps}\n      >\n        <TextInput\n          id={id}\n          width={inputWidth}\n          height={inputHeight}\n          disabled={disabled}\n          required={required}\n          isInvalid={isInvalid}\n          appearance={appearance}\n          placeholder={placeholder}\n          spellCheck={spellCheck}\n          {...remainingProps}\n        />\n      </FormField>\n    )\n  }\n}\n"