module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport { css } from 'ui-box'\nimport Transition from 'react-transition-group/Transition'\nimport { Pane, Card } from '../../layers'\nimport { Portal } from '../../portal'\nimport { Paragraph, Heading } from '../../typography'\nimport { Button, IconButton } from '../../buttons'\n\nconst animationEasing = {\n  deceleration: `cubic-bezier(0.0, 0.0, 0.2, 1)`,\n  acceleration: `cubic-bezier(0.4, 0.0, 1, 1)`,\n  spring: `cubic-bezier(0.175, 0.885, 0.320, 1.175)`\n}\n\nconst ANIMATION_DURATION = 240\n\nconst openAnimation = css.keyframes('openAnimation', {\n  from: {\n    transform: 'translateY(100%)'\n  },\n  to: {\n    transform: 'translateY(0)'\n  }\n})\n\nconst closeAnimation = css.keyframes('closeAnimation', {\n  from: {\n    transform: 'scale(1)',\n    opacity: 1\n  },\n  to: {\n    transform: 'scale(0.9)',\n    opacity: 0\n  }\n})\n\nconst animationStyles = {\n  '&[data-state=\"entering\"], &[data-state=\"entered\"]': {\n    animation: `${openAnimation} ${ANIMATION_DURATION}ms ${\n      animationEasing.spring\n    } both`\n  },\n  '&[data-state=\"exiting\"]': {\n    animation: `${closeAnimation} 120ms ${animationEasing.acceleration} both`\n  }\n}\n\nexport default class CornerDialog extends PureComponent {\n  static propTypes = {\n    /**\n     * Children can be a string, node or a function accepting `({ close })`.\n     * When passing a string, <Paragraph size={400} color=\"muted\" /> is used to wrap the string.\n     */\n    children: PropTypes.oneOfType([PropTypes.node, PropTypes.func]).isRequired,\n\n    /**\n     * The intent of the CornerDialog. Used for the button.\n     */\n    intent: PropTypes.oneOf(['none', 'success', 'warning', 'danger'])\n      .isRequired,\n\n    /**\n     * When true, the dialog is shown.\n     */\n    isShown: PropTypes.bool,\n\n    /**\n     * Title of the Dialog. Titles should use Title Case.\n     */\n    title: PropTypes.node,\n\n    /**\n     * Function that will be called when the exit transition is complete.\n     */\n    onCloseComplete: PropTypes.func,\n\n    /**\n     * Function that will be called when the enter transition is complete.\n     */\n    onOpenComplete: PropTypes.func,\n\n    /**\n     * When true, the footer with the cancel and confirm button is shown.\n     */\n    hasFooter: PropTypes.bool,\n\n    /**\n     * Function that will be called when the confirm button is clicked.\n     * This does not close the Dialog. A close function will be passed\n     * as a paramater you can use to close the dialog.\n     *\n     * `onConfirm={(close) => close()}`\n     */\n    onConfirm: PropTypes.func,\n\n    /**\n     * Label of the confirm button.\n     */\n    confirmLabel: PropTypes.string,\n\n    /**\n     * When true, the cancel button is shown.\n     */\n    hasCancel: PropTypes.bool,\n\n    /**\n     * Function that will be called when the cancel button is clicked.\n     * This closes the Dialog by default.\n     *\n     * `onCancel={(close) => close()}`\n     */\n    onCancel: PropTypes.func,\n\n    /**\n     * Label of the cancel button.\n     */\n    cancelLabel: PropTypes.string,\n\n    /**\n     * Width of the Dialog.\n     */\n    width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),\n\n    /**\n     * Props that are passed to the dialog container.\n     */\n    containerProps: PropTypes.object\n  }\n\n  static defaultProps = {\n    width: 392,\n    intent: 'none',\n    hasFooter: true,\n    confirmLabel: 'Learn More',\n    hasCancel: true,\n    cancelLabel: 'Close',\n    onCancel: close => close(),\n    onConfirm: close => close()\n  }\n\n  constructor(props) {\n    super(props)\n\n    this.state = {\n      exiting: false,\n      exited: !props.isShown\n    }\n  }\n\n  componentWillReceiveProps(nextProps) {\n    if (nextProps.isShown && !this.props.isShown) {\n      this.setState({\n        exited: false\n      })\n    }\n  }\n\n  handleExited = () => {\n    this.setState({ exiting: false, exited: true })\n    this.props.onCloseComplete()\n  }\n\n  handleClose = () => {\n    this.setState({ exiting: true })\n  }\n\n  handleConfirm = () => {\n    this.props.onConfirm(this.handleClose)\n  }\n\n  renderChildren = () => {\n    const { children } = this.props\n    if (typeof children === 'function') {\n      return children({ close: this.handleClose })\n    } else if (typeof children === 'string') {\n      return (\n        <Paragraph size={400} color=\"muted\">\n          {children}\n        </Paragraph>\n      )\n    }\n    return children\n  }\n\n  render() {\n    const {\n      title,\n      width,\n      intent,\n      isShown,\n      hasFooter,\n      hasCancel,\n      cancelLabel,\n      confirmLabel,\n      onOpenComplete,\n      containerProps\n    } = this.props\n\n    const { exiting, exited } = this.state\n\n    if (exited) return null\n\n    return (\n      <Portal>\n        <Transition\n          appear\n          unmountOnExit\n          timeout={ANIMATION_DURATION}\n          in={isShown && !exiting}\n          onExited={this.handleExited}\n          onEntered={onOpenComplete}\n        >\n          {state => (\n            <Card\n              role=\"dialog\"\n              backgroundColor=\"white\"\n              elevation={4}\n              width={width}\n              css={animationStyles}\n              data-state={state}\n              position=\"fixed\"\n              bottom={16}\n              right={16}\n              padding={32}\n              {...containerProps}\n            >\n              <Pane display=\"flex\" alignItems=\"center\" marginBottom={12}>\n                <Heading is=\"h4\" size={600} flex=\"1\">\n                  {title}\n                </Heading>\n                <IconButton\n                  height={32}\n                  icon=\"cross\"\n                  appearance=\"minimal\"\n                  onClick={this.handleClose}\n                />\n              </Pane>\n\n              <Pane overflowY=\"auto\" data-state={state}>\n                {this.renderChildren()}\n              </Pane>\n\n              {hasFooter && (\n                <Pane\n                  marginTop={24}\n                  flexShrink={0}\n                  display=\"flex\"\n                  flexDirection=\"row-reverse\"\n                >\n                  <Button\n                    appearance=\"primary\"\n                    intent={intent}\n                    marginLeft={8}\n                    onClick={this.handleConfirm}\n                  >\n                    {confirmLabel}\n                  </Button>\n                  {hasCancel && (\n                    <Button onClick={this.handleClose}>{cancelLabel}</Button>\n                  )}\n                </Pane>\n              )}\n            </Card>\n          )}\n        </Transition>\n      </Portal>\n    )\n  }\n}\n"