module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport Box, { spacing, position, layout, dimensions } from 'ui-box'\nimport { Text } from '../../typography'\nimport { withTheme } from '../../theme'\n\nconst CircleIcon = ({ size, fill = 'currentColor', ...props }) => (\n  <svg width={size} height={size} viewBox=\"0 0 10 10\" {...props}>\n    <circle fill={fill} cx=\"5\" cy=\"5\" r=\"5\" />\n  </svg>\n)\n\nCircleIcon.propTypes = {\n  fill: PropTypes.string,\n  size: PropTypes.number\n}\n\nclass Radio extends PureComponent {\n  static propTypes = {\n    /**\n     * Composes some Box APIs.\n     */\n    ...spacing.propTypes,\n    ...position.propTypes,\n    ...layout.propTypes,\n    ...dimensions.propTypes,\n\n    /**\n     * The id attribute of the radio.\n     */\n    id: PropTypes.string,\n\n    /**\n     * The name attribute of the radio.\n     */\n    name: PropTypes.string,\n\n    /**\n     * Label of the radio.\n     */\n    label: PropTypes.node,\n\n    /**\n     * The value attribute of the radio.\n     */\n    value: PropTypes.string,\n\n    /**\n     * Function called when state changes.\n     */\n    onChange: PropTypes.func,\n\n    /**\n     * When true, the radio is disabled.\n     */\n    disabled: PropTypes.bool,\n\n    /**\n     * When true, the radio is checked.\n     */\n    checked: PropTypes.bool,\n\n    /**\n     * The size of the radio circle. This also informs the text size and spacing.\n     */\n    size: PropTypes.oneOf([12, 16]),\n\n    /**\n     * When true, the radio get the required attribute.\n     */\n    isRequired: PropTypes.bool.isRequired,\n\n    /**\n     * When true, the aria-invalid attribute is true.\n     * Used for accessibility.\n     */\n    isInvalid: PropTypes.bool.isRequired,\n\n    /**\n     * The appearance of the checkbox.\n     * The default theme only comes with a default style.\n     */\n    appearance: PropTypes.string.isRequired,\n\n    /**\n     * Theme provided by ThemeProvider.\n     */\n    theme: PropTypes.object.isRequired\n  }\n\n  static defaultProps = {\n    appearance: 'default',\n    onChange: () => {},\n    size: 12,\n    isRequired: false,\n    isInvalid: false\n  }\n\n  render() {\n    const {\n      theme,\n\n      id,\n      name,\n      label,\n      disabled,\n      isInvalid,\n      checked,\n      onChange,\n      value,\n      size,\n      isRequired,\n      appearance,\n      ...props\n    } = this.props\n    const themedClassName = theme.getRadioClassName(appearance)\n\n    return (\n      <Box\n        is=\"label\"\n        cursor={disabled ? 'not-allowed' : 'pointer'}\n        display=\"flex\"\n        marginY={size === 12 ? 8 : 12}\n        {...props}\n      >\n        <Box\n          is=\"input\"\n          className={themedClassName}\n          id={id}\n          type=\"radio\"\n          name={name}\n          value={value}\n          checked={checked}\n          onChange={e => onChange(e.target.value)}\n          disabled={disabled}\n          aria-invalid={isInvalid}\n          required={isRequired}\n        />\n        <Box\n          boxSizing=\"border-box\"\n          borderRadius={9999}\n          display=\"flex\"\n          alignItems=\"center\"\n          justifyContent=\"center\"\n          marginTop={size === 12 ? 2 : 3}\n          width={size}\n          height={size}\n        >\n          <CircleIcon size={size === 12 ? 4 : 4} />\n        </Box>\n        {label && (\n          <Text\n            marginLeft={size === 12 ? 8 : 10}\n            size={size === 12 ? 300 : 400}\n            color={disabled ? 'muted' : 'default'}\n          >\n            {label}\n          </Text>\n        )}\n      </Box>\n    )\n  }\n}\n\nexport default withTheme(Radio)\n"