module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport { Text } from '../../typography'\nimport TableCell from './TableCell'\n\nconst ellipsis = {\n  overflow: 'hidden',\n  textOverflow: 'ellipsis',\n  whiteSpace: 'nowrap'\n}\n\nexport default class TextTableCell extends PureComponent {\n  static propTypes = {\n    /**\n     * Composes the TableCell component as the base.\n     */\n    ...TableCell.propTypes,\n\n    /**\n     * Adds textAlign: right and fontFamily: mono.\n     */\n    isNumber: PropTypes.bool.isRequired,\n\n    /**\n     * Pass additional props to the Text component.\n     */\n    textProps: PropTypes.object\n  }\n\n  static defaultProps = {\n    isNumber: false\n  }\n\n  render() {\n    const { children, textProps, isNumber, ...props } = this.props\n\n    return (\n      <TableCell {...props}>\n        <Text\n          size={300}\n          flex=\"1\"\n          {...ellipsis}\n          {...(isNumber ? { fontFamily: 'mono' } : {})}\n          {...textProps}\n        >\n          {children}\n        </Text>\n      </TableCell>\n    )\n  }\n}\n"