module.exports = "import React, { PureComponent } from 'react'\nimport Box, { splitBoxProps } from 'ui-box'\nimport { Icon } from '../../icon'\nimport { TextInput } from '../../text-input'\nimport { withTheme } from '../../theme'\nimport { StackingOrder } from '../../constants'\n\nclass SearchInput extends PureComponent {\n  static propTypes = {\n    /**\n     * Composes the TextInput component as the base.\n     */\n    ...TextInput.propTypes\n  }\n\n  static defaultProps = {\n    height: 32,\n    appearance: 'default'\n  }\n\n  render() {\n    const { theme, appearance, disabled, height, ...props } = this.props\n    const { matchedProps, remainingProps } = splitBoxProps(props)\n    const { width } = matchedProps\n    const iconSize = theme.getIconSizeForInput(height)\n\n    return (\n      <Box\n        position=\"relative\"\n        display=\"inline-flex\"\n        height={height}\n        {...matchedProps}\n      >\n        <Box\n          height={height}\n          width={height}\n          pointerEvents=\"none\"\n          position=\"absolute\"\n          display=\"flex\"\n          justifyContent=\"center\"\n          alignItems=\"center\"\n        >\n          <Icon\n            icon=\"search\"\n            color=\"default\"\n            zIndex={StackingOrder.FOCUSED + 1}\n            size={iconSize}\n          />\n        </Box>\n        <TextInput\n          height={height}\n          paddingLeft={height}\n          appearance={appearance}\n          disable={disabled}\n          width={width}\n          {...remainingProps}\n        />\n      </Box>\n    )\n  }\n}\n\nexport default withTheme(SearchInput)\n"