module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport cx from 'classnames'\nimport { withTheme } from '../../theme'\nimport Text from './Text'\n\nclass Code extends PureComponent {\n  static propTypes = {\n    ...Text.propTypes,\n\n    /**\n     * The appearance of the code.\n     */\n    appearance: PropTypes.oneOf(['default', 'minimal']).isRequired,\n\n    /**\n     * Theme provided by ThemeProvider.\n     */\n    theme: PropTypes.object.isRequired,\n\n    /**\n     * Class name passed to the button.\n     * Only use if you know what you are doing.\n     */\n    className: PropTypes.string\n  }\n\n  static defaultProps = {\n    appearance: 'default'\n  }\n\n  render() {\n    const { theme, className, appearance, ...props } = this.props\n\n    const {\n      className: themedClassName = '',\n      ...themeProps\n    } = theme.getCodeProps(appearance)\n\n    return (\n      <Text\n        is=\"code\"\n        className={cx(className, themedClassName)}\n        fontFamily=\"mono\"\n        {...themeProps}\n        {...props}\n      />\n    )\n  }\n}\n\nexport default withTheme(Code)\n"