module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport cx from 'classnames'\nimport { Pane } from '../../layers'\nimport { withTheme } from '../../theme'\nimport { TableRowProvider } from './TableRowContext'\n\nclass TableRow extends PureComponent {\n  static propTypes = {\n    /**\n     * Composes the Pane component as the base.\n     */\n    ...Pane.propTypes,\n\n    /**\n     * The height of the row. Remember to add paddings when using \"auto\".\n     */\n    height: PropTypes.oneOfType([PropTypes.number, PropTypes.string])\n      .isRequired,\n\n    /**\n     * Function that is called on click and enter/space keypress.\n     */\n    onSelect: PropTypes.func,\n\n    /**\n     * Function that is called on click and enter/space keypress.\n     */\n    onDeselect: PropTypes.func,\n\n    /**\n     * Makes the TableRow selectable.\n     */\n    isSelectable: PropTypes.bool,\n\n    /**\n     * Makes the TableRow selected.\n     */\n    isSelected: PropTypes.bool,\n\n    /**\n     * Manually set the TableRow to be highlighted.\n     */\n    isHighlighted: PropTypes.bool,\n\n    /**\n     * The intent of the alert.\n     */\n    intent: PropTypes.oneOf(['none', 'success', 'warning', 'danger'])\n      .isRequired,\n\n    /**\n     * The appearance of the table row. Default theme only support default.\n     */\n    appearance: PropTypes.string.isRequired,\n\n    /**\n     * Theme provided by ThemeProvider.\n     */\n    theme: PropTypes.object.isRequired,\n\n    /**\n     * Class name passed to the button.\n     * Only use if you know what you are doing.\n     */\n    className: PropTypes.string\n  }\n\n  static defaultProps = {\n    intent: 'none',\n    appearance: 'default',\n    height: 48,\n    onClick: () => {},\n    onSelect: () => {},\n    onDeselect: () => {},\n    onKeyPress: () => {}\n  }\n\n  handleClick = e => {\n    this.props.onClick(e)\n    if (this.props.isSelectable) {\n      if (this.props.isSelected) {\n        this.props.onDeselect()\n      } else {\n        this.props.onSelect()\n      }\n    }\n  }\n\n  handleKeyPress = e => {\n    if (this.props.isSelectable) {\n      if (e.key === 'Enter' || e.key === ' ') {\n        this.props.onSelect()\n        e.preventDefault()\n      }\n    }\n\n    this.props.onKeyPress(e)\n  }\n\n  render() {\n    const {\n      theme,\n      className,\n      height,\n      children,\n      intent,\n      appearance,\n\n      // Filter out\n      onClick,\n      onKeyPress,\n      onSelect,\n      onDeselect,\n\n      isHighlighted,\n      isSelectable,\n      isSelected,\n      ...props\n    } = this.props\n\n    const themedClassName = theme.getRowClassName(appearance, intent)\n\n    return (\n      <TableRowProvider height={height}>\n        <Pane\n          className={cx(themedClassName, className)}\n          display=\"flex\"\n          aria-selected={isHighlighted}\n          aria-current={isSelected}\n          data-isselectable={isSelectable}\n          {...(isSelectable\n            ? {\n                tabIndex: 0\n              }\n            : {})}\n          onClick={this.handleClick}\n          onKeyPress={this.handleKeyPress}\n          height={height}\n          borderBottom=\"muted\"\n          {...props}\n        >\n          {children}\n        </Pane>\n      </TableRowProvider>\n    )\n  }\n}\n\nexport default withTheme(TableRow)\n"