module.exports = "import React, { PureComponent } from 'react'\nimport PropTypes from 'prop-types'\nimport Box, { dimensions, spacing, position, layout } from 'ui-box'\nimport { Autocomplete } from '../../autocomplete'\nimport { TextInput } from '../../text-input'\nimport { IconButton } from '../../buttons'\n\nexport default class Combobox extends PureComponent {\n  static propTypes = {\n    /**\n     * Implements some APIs from ui-box.\n     */\n    ...dimensions.propTypes,\n    ...spacing.propTypes,\n    ...position.propTypes,\n    ...layout.propTypes,\n\n    /**\n     * The options to show in the menu.\n     */\n    items: PropTypes.array.isRequired,\n\n    /**\n     * The selected item when controlled.\n     */\n    selectedItem: PropTypes.any,\n\n    /**\n     * Function called when value changes.\n     */\n    onChange: PropTypes.func,\n\n    /**\n     * When true, open the autocomplete on focus.\n     */\n    openOnFocus: PropTypes.bool,\n\n    /**\n     * Default selected item when uncontrolled.\n     */\n    defaultSelectedItem: PropTypes.any,\n\n    /**\n     * The placeholder text when there is no value present.\n     */\n    placeholder: PropTypes.string,\n\n    /**\n     * In case the array of items is not an array of strings,\n     * this function is used on each item to return the string that will be shown on the filter\n     */\n    itemToString: PropTypes.func,\n\n    /**\n     * Properties forwarded to the input. Use with caution.\n     */\n    inputProps: PropTypes.object,\n\n    /**\n     * Properties forwarded to the button. Use with caution.\n     */\n    buttonProps: PropTypes.object,\n\n    /**\n     * Properties forwarded to the autocomplete component. Use with caution.\n     */\n    autocompleteProps: PropTypes.object\n  }\n\n  static defaultProps = {\n    width: 240,\n    openOnFocus: false\n  }\n\n  constructor(props, context) {\n    super(props, context)\n    this.state = {\n      isOpenedByButton: false\n    }\n  }\n\n  handleStateChange = changes => {\n    if (Object.prototype.hasOwnProperty.call(changes, 'isOpen')) {\n      if (!changes.isOpen) {\n        this.setState({ isOpenedByButton: false })\n      }\n    }\n  }\n\n  render() {\n    const {\n      items,\n      selectedItem,\n      defaultSelectedItem,\n      itemToString,\n      width,\n      height,\n      onChange,\n      placeholder,\n      inputProps,\n      buttonProps,\n      openOnFocus,\n      autocompleteProps,\n      ...props\n    } = this.props\n\n    return (\n      <Autocomplete\n        items={items}\n        selectedItem={selectedItem}\n        defaultSelectedItem={defaultSelectedItem}\n        itemToString={itemToString}\n        onChange={onChange}\n        onStateChange={this.handleStateChange}\n        isFilterDisabled={this.state.isOpenedByButton}\n        {...autocompleteProps}\n      >\n        {({\n          getRef,\n          isShown,\n          openMenu,\n          inputValue,\n          getInputProps,\n          getButtonProps,\n          clearSelection\n        }) => (\n          <Box\n            innerRef={ref => getRef(ref)}\n            display=\"inline-flex\"\n            width={width}\n            {...props}\n          >\n            <TextInput\n              width={0}\n              flex={1}\n              height={height}\n              value={inputValue}\n              borderTopRightRadius={0}\n              borderBottomRightRadius={0}\n              {...getInputProps({\n                ...inputProps,\n                placeholder,\n                onFocus: () => {\n                  if (openOnFocus) openMenu()\n                },\n                onChange: e => {\n                  if (this.state.isOpenedByButton) {\n                    this.setState({\n                      isOpenedByButton: false\n                    })\n                  }\n                  if (e.target.value.trim() === '') {\n                    // Prevent the selected item from sticking around\n                    clearSelection()\n                  }\n                }\n              })}\n            />\n            <IconButton\n              iconAim=\"down\"\n              color=\"muted\"\n              icon=\"caret-down\"\n              appearance=\"default\"\n              height={height}\n              marginLeft={-1}\n              paddingLeft={0}\n              paddingRight={0}\n              borderTopLeftRadius={0}\n              borderBottomLeftRadius={0}\n              {...getButtonProps({\n                ...buttonProps,\n                onClick: () => {\n                  if (!isShown) {\n                    this.setState({ isOpenedByButton: true })\n                  }\n                }\n              })}\n            />\n          </Box>\n        )}\n      </Autocomplete>\n    )\n  }\n}\n"